import Vue from 'vue'
import Vuex from 'vuex'

import api from '../api_request'	//API
import router from '../router'	//
import cache from '../setting_cache_time.js'	//

Vue.use(Vuex)

const TOKEN = cache.get_cache("token") || ""		//TOKEN
const OPENID = cache.get_cache("openId") || ""		//OPENID
const USER_INFO = cache.get_cache("userInfo") || {}	//USERINFO

//
const state = {
	token: TOKEN,		// token
	openId: OPENID,		// openid
	userInfo: USER_INFO	// 
}

//
const mutations = {
   set_token(state, token) {
   	state.token = token
	cache.set_cache("token", token)
   },
   set_openid(state, openId) {
   	state.openId = openId
	cache.set_cache("openId", openId)
   },
   set_userinfo(state, userInfo) {
   	state.userInfo = userInfo
	cache.set_cache("userInfo", userInfo)
   },
   set_logout(state) {
   	state.token = ""
	cache.set_cache("token", "")
   }
}

//get
const getters = {
   // 
   hasLogin: state => {
   	if (state.token) {
   		return true
   	} else {
   		return false
   	}
   }
}

//
const actions = {
	//
	to_hideload({commit}){
		uni.hideLoading()
	},
	//load
	to_load({commit},msg = 'loading...'){
		 uni.showLoading({
			title: msg
		})
	},
	//toast
	to_msg({commit},msg = '...'){
		uni.showToast({  
			title: msg,
			icon:'none'
		})
	},
	//
	goto_page({commit},url,type = 1){
		console.log("type="+type)
		console.log("url="+url)
		if(type == 1) uni.navigateTo({url:url})
		if(type == 2) uni.redirectTo({url:url})
		if(type == 3) uni.reLaunch({url:url})
		if(type == 4) uni.switchTab({url:url})
		if(type == 5) uni.navigateBack()
	},
   //  
   reLogin({commit}, info) { 
		commit("set_token","")
		uni.redirectTo({
			url: routes.index
		})
   },
   //
   async login_in({commit},info) {
	   store.dispatch("to_load")
	   let r = await api.app_login(info)
	   if(!r){
		   console.log("r="+r)
		   console.log("info="+JSON.stringify(info))
	   }else{
		  console.log("ok------------")
		  console.log(r.access_token)
		  commit("set_token",r.access_token)
		  store.dispatch("to_hideload")
		  store.dispatch("goto_page",router.index,2)
	   }
   },
   register({commit},info){
	   let r = api.app_register(info)
   }
   
}
  
const store = new Vuex.Store({
	state,
	mutations,
	actions,
	getters
})

Vue.prototype.is_vuex = store
Vue.prototype.is_router = router
Vue.prototype.is_cache = cache

export default store