/**
 * uni-app
 *  Promise  request 
 */

export default {
	config: {
		baseUrl: "",
		header: {
			'Content-Type':'application/json;charset=UTF-8',
			'Content-Type':'application/x-www-form-urlencoded'
		},  
		data: {},
		method: "GET",
		dataType: "json",  /* json JSON.parse */
		responseType: "text",
		success() {},
		fail() {},
		complete() {}
	},
	interceptor: {
		request: null,
		response: null
	},
	request(options) {
		if (!options) {
			options = {}
		}
		options.baseUrl = options.baseUrl || this.config.baseUrl
		options.dataType = options.dataType || this.config.dataType
		options.url = options.baseUrl + options.url
		options.data = options.data || {}
		options.method = options.method || this.config.method
		//TODO 
		
		//TODO 
		/* 
		_token = {'token': getStorage(STOREKEY_LOGIN).token || 'undefined'},
		_sign = {'sign': sign(JSON.stringify(options.data))}
		options.header = Object.assign({}, options.header, _token,_sign) 
		*/
	   
		return new Promise((resolve, reject) => {
			let _config = null
			let dev = process.env.NODE_ENV
			
			options.complete = (response) => {
				let statusCode = response.statusCode
				response.config = _config
				if (this.interceptor.response) {
					let newResponse = this.interceptor.response(response)
					if (newResponse) {
						response = newResponse
					}
				}
				// 
				if (dev === 'development'){
					_reslog(response)
				}
				
				if (statusCode === 200) { //
					resolve(response);
				} else {
					reject(response)
				}
			}

			_config = Object.assign({}, this.config, options)
			_config.requestId = new Date().getTime()

			if (this.interceptor.request) {
				this.interceptor.request(_config)
			}

			if (dev === 'development') {
				
				// 
				_reqlog(_config)
				
// 				console.log("" + _config.requestId + " " + _config.url)
// 				if (_config.data) {
// 					console.log("" + _config.requestId + " " + JSON.stringify(_config.data))
// 				}
			}

			uni.request(_config);
		});
	},
	get(url, data, options) {
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'GET'  
		return this.request(options)
	},
	post(url, data, options) {
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'POST'
		return this.request(options)
	},
	put(url, data, options) {
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'PUT'
		return this.request(options)
	},
	delete(url, data, options) {
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'DELETE'
		return this.request(options)
	}
}

let reqid = null

/**
 * 
 */
function _reqlog(req) {
	if (process.env.NODE_ENV === 'development') {
		reqid = req.requestId
		console.log("" + req.requestId + " " + req.url)
		if (req.data) {
			console.log("" + req.requestId + " " + JSON.stringify(req.data))
		}
	}
	//TODO 
}

/**
 * 
 */
function _reslog(res) {
	let _statusCode = res.statusCode;
	if (process.env.NODE_ENV === 'development') {
		// console.log("" + res.config.requestId + " " + res.config.url)
// 		if (res.config.data) {
// 			console.log("" + res.config.requestId + " " + JSON.stringify(res.config.data))
// 		}
		console.log("" + reqid + " " + JSON.stringify(res))
	}
	//TODO 
	switch(_statusCode){
		case 200:
			break;
		case 401:
			break;
		case 404:
			break;
		default:
			break;
	}
}

